import styled from 'styled-components';
import Icon from '../../../components/Icon';
import React, {ChangeEventHandler, useState} from 'react';
import store from '../../../store/store';
import dayjs from 'dayjs';


const Wrapper = styled.section`
  margin-top: 20%;
  margin-left: 5%;
  margin-right: 5%;

  > label {
    display: flex;
    justify-content: space-between;
    align-items: center;

    > .icon {
      width: 2em;
      height: 2em;
      margin-right: 2%;
    }

    > div {
      font-size: 16px;
      flex-grow: 1;
    }

    > input {
      width: 40%;
      display: block;
      outline: none;
      border: none;
      height: 50px;
    }
  }

  > div {
    display: flex;
    justify-content: space-between;
    align-items: center;

    > .icon {
      width: 2em;
      height: 2em;
      margin-right: 2%;
    }

    > .time {
      font-size: 16px;
      flex-grow: 1;
    }

    > input {
      -webkit-appearance: none;
      outline: none;
      font-weight: bold;
      color: #5084D4;
      border:none ;
      width: 50%;
      font-size: 13px;
    }


    > div:last-child {
      border: solid 1px red;
      width: 50%;
      height: 50px;
      text-align: center;
    }
  }
`;


const Remarks: React.FunctionComponent = () => {

  const [remark, setRemark] = useState('');
  const onchange: ChangeEventHandler<HTMLInputElement> = (event) => {
    setRemark(event.target.value);
  };
  React.useEffect(
    () => {
      const action = {
        type: 'text_remark',
        remark: remark
      };
      store.dispatch(action);
    }, [remark]
  );

  const A = dayjs(new Date().toISOString()).format('YYYY-MM-DD');
  const [onTime, setonTime] = useState(A);
  const oninput: ChangeEventHandler<HTMLInputElement> = (event) => {
    console.log(event.target.value);
    setonTime(event.target.value);
  };

  React.useEffect(
    () => {
      const action = {
        type: 'text_time',
        onTime: onTime
      };
      store.dispatch(action);
    }, [onTime]
  );


  return (
    <Wrapper>
      <label>
        <Icon name="beizhu"/>
        <div>
          备注
        </div>
        <input type="text" placeholder="请填写备注"
               value={remark}
               onChange={onchange}/>
      </label>
      <hr/>
      <div>
        <Icon name="rili"/>
        <div className="time">
          时间
        </div>
        <input type="date"
               value={onTime}
               onInput={oninput}
        >
        </input>
      </div>
    </Wrapper>
  );


};


export {Remarks};