import React from 'react';
import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect
} from 'react-router-dom';
import Details from './views/Details';
import Bookkeeping from './views/bookkeeping/Bookkeeping';
import Statistics from './views/Statistics/Statistics';
import NoMatch from './views/NoMatch';
import Increase from './views/Increase/Increase';
import Modifies from './views/Modifies/Modifies';
import styled from 'styled-components';

const Wrapper = styled.div`
  max-width: 411px;
  margin: 0 auto;
`;


function App() {
  return (
    <Wrapper>


      <Router>
        <Switch>
          <Route exact path="/Details">
            <Details/>
          </Route>
          <Route exact path="/bookkeeping">
            <Bookkeeping/>
          </Route>
          <Route exact path="/bookkeeping/:id">
            <Modifies/>
          </Route>
          <Route exact path="/Increase">
            <Increase/>
          </Route>
          <Redirect exact from="/" to="/bookkeeping"/>
          <Route path="/statistics">
            <Statistics/>
          </Route>
          <Route path="*">
            <NoMatch/>
          </Route>


        </Switch>
      </Router>
    </Wrapper>
  );
}


export default App;
