function bookeeepingreducer(state = [], action: any) {
  if (action.type === 'add_save') {
    if (window.localStorage.getItem('save')) {
      const getSave = JSON.parse(window.localStorage.getItem('save') as string);
      const ASave = [...getSave, action.value];
      window.localStorage.setItem('save', JSON.stringify(ASave));
      return ASave;
    } else {
      const ASave = [action.value];
      console.log(ASave);
      window.localStorage.setItem('save', JSON.stringify(ASave));
      return ASave;
    }
  }
  return state;
}

export default bookeeepingreducer;
