import Layout from '../../components/Layout';
import React, {useState} from 'react';
import ReactEcharts from './ReactEcharts';
import {EChartOption} from 'echarts';
import styled from 'styled-components';


const Wrapper = styled.section`
  background: #E7E9Ef;
  height: 100%;
  display: flex;
  flex-direction: column;

  > ol {
    background: #5084D4;
    display: flex;
    justify-content: center;
    align-content: center;
    text-align: center;
    height: 50px;

    > li {
      width: 30%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      &.selected {
        color: white;
      }

      &.selected:after {
        content: '';
        display: block;
        height: 3px;
        background: red;
        position: absolute;
        bottom: 0;
        width: 100%;
        left: 0;
      }
    }
  }
  >div{
    margin: 20px;
    text-align: center;
  }
  
`;


function Statistics() {

  const tagMap = {'-': '支出', '+': '收入'};
  const [tagList] = useState<('-' | '+')[]>(['-', '+']);
  const [tag, setTag] = useState('-');
  const [qqq, setqqq] = useState(false);

  const onNav = (c: string) => {
    setTag(c);
    setqqq(!qqq);
  };

  type Save = { createAt: string; index: number; icon: string; tag: string; numberpad: number; text: string; iconID: number; name: string }
  const hash: { [k: string]: number[] } = {};
  const A = JSON.parse(window.localStorage.getItem('save') as string);
  const AAAAA: any = [];
  if (A) {
  const B = A.filter((r: { tag: string; }) => r.tag === tag);
  B.map((r: Save) => {
    const key = r.name;
    if (!(key in hash)) {
      hash[key] = [];
    }
    hash[key].push(r.numberpad);
    return '';
  });
  const hashTWo: { [CC: string]: number } = {};
  Object.entries(hash).map(
    (r) => {
      const KK = r[0];
      // eslint-disable-next-line
      hashTWo[KK] = eval(r[1].join('+'));
      return '';
    }
  );
  const arr = Object.entries(hashTWo);

  arr.map(R => {
    const obj: { value?: number, name?: string } = {};
    obj.name = R[0];
    obj.value = R[1];
    AAAAA.push(obj);
    return '';
  });
}



  const option: EChartOption = {
    title: {
      text: qqq ? '总支出比例' : '总收入比例',
      left: 'center'
    },
    tooltip: {
      trigger: 'item'
    },
    legend: {
      orient: 'vertical',
      left: 'left',
    },
    series: [
      {
        name: '我的',
        type: 'pie',
        radius: '50%',
        data: AAAAA,
        emphasis: {
          itemStyle: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      }
    ]
  };



  return (
    <Layout>
      <Wrapper>
        <ol>
          {
            tagList.map(t =>
              <li key={t}
                  className={tag === t ? 'selected' : ''}
                  onClick={() => onNav(t)}
              >
                {tagMap[t]}
              </li>
            )
          }
        </ol>
        {
          A ? <ReactEcharts option={option}/> : <div>
            当前数据为空,请记一笔!
          </div>
        }

      </Wrapper>
    </Layout>
  );
}

export default Statistics;