import createId from '../../lib/createId';

type IconTypes = {
  id: number,
  icon: string,
  name: string
}


const icon: IconTypes[] = [
  {id: createId(), icon: 'fushi', name: '服饰'},
  {id: createId(), icon: 'gongzi', name: '工资'},
  {id: createId(), icon: 'gouwu', name: '购物'},
  {id: createId(), icon: 'hongbao', name: '红包'},
  {id: createId(), icon: 'jianzhi', name: '兼职'},
  {id: createId(), icon: 'jiaotong', name: '交通'},
  {id: createId(), icon: 'lingshi', name: '零食'},
  {id: createId(), icon: 'shiwu', name: '食物'},
  {id: createId(), icon: 'tongxun', name: '通讯'},
  {id: createId(), icon: 'touzi', name: '投资'},
  {id: createId(), icon: 'xuexi', name: '学习'},
  {id: createId(), icon: 'yule', name: '娱乐'},
  {id: createId(), icon: 'yundong', name: '运动'},
  {id: createId(), icon: 'zufang', name: '租房'},
];


function iconreducer(state = icon, action: any) {
  if (action.type === 'add_icon') {
    if (window.localStorage.getItem('icon')) {
      const getIcon = JSON.parse(window.localStorage.getItem('icon') as string);


      const newstate = [...getIcon, action.value];
      window.localStorage.setItem('icon', JSON.stringify(newstate));
      return newstate;
    } else {
      const newstate = [...state, action.value];
      window.localStorage.setItem('icon', JSON.stringify(newstate));
      return newstate;
    }
  }
  if (action.type === 'delete_icon') {
    const cloneState = JSON.parse(JSON.stringify(state));
    const newState = cloneState.filter((t: { id: any }) => t.id !== action.tagId);
    window.localStorage.setItem('icon', JSON.stringify(newState));
    return newState;
  }
  if (action.type === 'modifies') {
    if (window.localStorage.getItem('icon')){
      const getIcon = JSON.parse(window.localStorage.getItem('icon') as string);
      let result = -1;
      for (let i = 0; i < getIcon.length; i++) {
        if (getIcon[i].id === action.value.id) {
          result = i;
          break;
        }
      }
      getIcon.splice(result, 1, action.value);
      window.localStorage.setItem('icon', JSON.stringify(getIcon));
      return getIcon;
    }else {
      const cloneIcon = JSON.parse(JSON.stringify(state));
      let result = -1;
      for (let i = 0; i < cloneIcon.length; i++) {
        if (state[i].id === action.value.id) {
          result = i;
          break;
        }
      }
      cloneIcon.splice(result, 1, action.value);
      window.localStorage.setItem('icon', JSON.stringify(cloneIcon));
      return cloneIcon;
    }
  }

  return state;
}

export default iconreducer;
