import React, {useState} from 'react';
import {Digital} from './Digital';
import store from '../../../../store/store';
import Icon from '../../../../components/Icon';


const DigitalStorage: React.FunctionComponent = () => {

  const [Switch, setSwitch] = useState<Boolean>(false);
  const [iconName, setName] = useState('fushi');

  let update = () => {
    const  getIcon = JSON.parse(window.localStorage.getItem('icon') as string)
  if (getIcon){
    getIcon.map(
      function (t: { id: number; icon: string; }) {
        if (t.id === store.getState().middle.index) {
          setName(t.icon);
        }
        return '';
      }
    );
  }else {
    store.getState().icon.map(
      function (t: { id: number; icon: string; }) {
        if (t.id === store.getState().middle.index) {
          setName(t.icon);
        }
        return '';
      }
    );
  }
  }
store.subscribe(update);

  const onSwitch = () => {
    setSwitch(!Switch);
  };
  const [output, setoutput] = useState<string>('0');
  const generateOutput = (text: string, output: string) => {
    switch (text) {
      case '0':
      case '1':
      case '2':
      case '3':
      case '4':
      case '5':
      case '6':
      case '7':
      case '8':
      case '9':
        if (output === '0') {
          return text;
        } else {
          return output + text;
        }

      case '.':
        console.log(typeof output);
        if (output.indexOf('.') >= 0) {
          return output;
        } else {
          return output + '.';
        }
      case 'C':
        return '0';
      default:
        return '';
    }
  };

  function onClickButtonWrapper(e: React.MouseEvent) {
    const text: string | null = (e.target as HTMLButtonElement).textContent;
    let Temporary = generateOutput(typeof text === 'string' ? text : '0', output);
    if (Temporary.length > 16) {
      return Temporary.slice(0, 16);
    }
    const action = {
      type: 'number_pad',
      numberpad: Temporary,
    };
    store.dispatch(action);
    setoutput(Temporary);
  }


  const A = ()=>{
    if (store.getState().middle.numberpad === ''){
      setoutput('0');
      VV()
    }
  }
let VV =  store.subscribe(A)









  const NumberPad = (
    <div className="NumberPad">
      <div className="complete"
           onClick={onSwitch}
      >
        完成
      </div>
      <div className="pad" onClick={onClickButtonWrapper}>
        <button>1</button>
        <button>2</button>
        <button>3</button>
        <button>4</button>
        <button>5</button>
        <button>6</button>
        <button>7</button>
        <button>8</button>
        <button>9</button>
        <button>.</button>
        <button>0</button>
        <button>C</button>
      </div>
    </div>);

  return (
    <Digital>
      <div className="DigitalStorage">
        <div>
          <Icon name={iconName}/>
        </div>
        <div onClick={onSwitch}>
          {output}
        </div>
      </div>
      {Switch ? NumberPad : ''}
    </Digital>
  );
};


export {DigitalStorage};