import React, {ChangeEventHandler, useState} from 'react';
import styled from 'styled-components';
import Icon from '../../components/Icon';
import {NavLink} from 'react-router-dom';
import store from '../../store/store';


const Wrapper = styled.section`

height: 100vh;
display: flex;
flex-direction: column;
  >div:first-child{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10%;
    background:  #5A97F4;
    color: white;
    >a  div{
    padding-left: 10%;
    display: flex;
    justify-content: center;
    align-content: center;
      >.icon{
      margin-left: 2%;
      width: 2em;
      height: 2em;
      }
    }
    >.text{
    text-align: center;
    flex-grow: 1;
    }
    >.add{
      padding-right: 2%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

  }
  >.addIcon{
  display: flex;
  height: 5%;
  align-content: center;
  margin-top:4%;

  
  >.icon{
  width: 2em;
  height: 2em;
  margin-left: 3%;
  margin-right: 3%;
  }
  >input{
  flex-grow: 1;
  outline:none;
  border: none;
  }
}
  >.hr{
height: 5%;
padding: 3%   0   5%  0;
}
  >ol{
  display:flex;
  height: 50%;
  flex-wrap: wrap;
  >li{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20%;
  margin-left: 5%;
  >.icon{
  width: 3em;
  height: 3em;
  display: block;
  }
  }
  }
  
  
  
`;


const Increase: React.FunctionComponent = () => {
  const iconMap = [
    {icon: 'fushi'},
    {icon: 'gongzi'},
    {icon: 'gouwu'},
    {icon: 'hongbao'},
    {icon: 'jianzhi'},
    {icon: 'jiaotong'},
    {icon: 'lingshi'},
    {icon: 'shiwu'},
    {icon: 'tongxun'},
    {icon: 'touzi'},
    {icon: 'xuexi'},
    {icon: 'yule'},
    {icon: 'yundong'},
    {icon: 'zufang'},
  ];

  const [icon] = useState(iconMap);
  const [iconName, setIconName] = useState('fushi');
  const [iconText, setIconText] = useState('');
  const onIcon = (icon: string) => {
    setIconName(icon);
  };
  const onchange: ChangeEventHandler<HTMLInputElement> = (event) => {
    setIconText(event.target.value);
  };

  const onAdd = () => {
    if (iconText) {
      const id = parseInt(window.localStorage.getItem('createID') as string);
      const newID = id + 1;
      const action = {
        type: 'add_icon',
        value: {
          id: newID,
          icon: iconName,
          name: iconText
        }
      };
      store.dispatch(action);
      window.localStorage.setItem('createID', JSON.stringify(newID));
    } else {
      alert('类别不可以为空');
    }
  };


  return (
    <Wrapper>
      <div>
        <NavLink to="/bookkeeping">
          <div>
            <Icon name="leftArrow"/>
          </div>
        </NavLink>
        <div className="text">
          添加支出类别
        </div>
        <div className='add' onClick={onAdd}>
          <NavLink to="/bookkeeping">
            <div>
              完成
            </div>
          </NavLink>
        </div>

      </div>
      <div className="addIcon">
        <Icon name={iconName}/>
        <input placeholder="请输入类别名称(尽量不超过两个字)"
               value={iconText}
               onChange={onchange}
        />
      </div>
      <div className="hr">
        <hr/>
      </div>

      <ol>
        {icon.map(t =>
          <li key={t.icon} onClick={() => onIcon(t.icon)}>
            <Icon name={t.icon}/>
          </li>
        )}
      </ol>
    </Wrapper>
  );
};


export default Increase;

