import React, {ChangeEventHandler, useState} from 'react';
import {
  NavLink,
  useParams
} from 'react-router-dom';
import styled from 'styled-components';
import Icon from '../../components/Icon';
import store from '../../store/store';


const Wrapper = styled.section`
height: 100vh;
>div:first-child{
    display: flex;
    height: 10%;
    background:  #5A97F4;
    color: white;
    align-items: center;
    justify-content: space-around;
    >a{
      >.icon{
      padding-left: 2%;
      width: 2em;
      height: 2em;
       }
    }
    >.text{
      display: flex;
      flex-grow: 1;
      justify-content: center;
      align-items: center;
     
    }


}
>div:last-child{
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    >.icon{
    width: 3em;
    height: 3em;
    margin: 5%;
    }
    >input{
       outline:none;
       border:solid  1px   rgb(231, 233, 239);
       margin: 5%;
       height: 5%;
       border-radius: 10px;
       padding-left: 3%;
    }
    
    >button{
    margin: 5%;
    width: 30%;
    height: 5%;
    border-radius: 14px;
    outline:none;
    border: none;
    background: #7db2f8;
    color: white;
    }
    

}
`;


const Modifies: React.FunctionComponent = () => {
  type Pops = {
    id: string
  }
  let {id} = useParams<Pops>();

  const [iconID] = useState(id);
  const [iconTag, setIconTag] = useState('');
  const [iconName, setIconName] = useState('');


    React.useEffect(
      () => {
        let iconIndex = store.getState().icon;
        let icon = iconIndex.filter(
          (t: { id: number; }) => t.id === parseInt(iconID)
        );
        setIconTag(icon[0].icon);
        setIconName(icon[0].name);
      },[iconID]
    );

  const onChange: ChangeEventHandler<HTMLInputElement> = (event) => {
    setIconName(event.target.value);
  };
  console.log(iconName);

  const onIcon = () => {
    const action = {
      type: 'modifies',
      value: {
        id: parseInt(iconID),
        icon: iconTag,
        name: iconName
      }
    };
    store.dispatch(action);
    alert('修改成功请返回')
  };


  return (
    <Wrapper>
      <div>
        <NavLink to='/bookkeeping'>
          <Icon name="leftArrow"/>
        </NavLink>

        <div className="text">
          <div>
            编辑标签页
          </div>
        </div>
        <div>

        </div>
      </div>
      <div>
        <Icon name={iconTag}/>
        <input value={iconName}
               onChange={onChange}
        />

        <button onClick={onIcon}>
          保存
        </button>
      </div>

    </Wrapper>
  );

};

export default Modifies;