import Icon from '../../../../components/Icon';
import React, {useState} from 'react';
import store from '../../../../store/store';
import {NavLink} from 'react-router-dom';
import {Tags} from './Tags';


const TagsSection: React.FunctionComponent = () => {

  const  windowIcon =JSON.parse(window.localStorage.getItem('icon') as string)
  const [tagSwitch, settagSwitch] = useState(false);
  const [editorSwitch, seteditorSwitch] = useState(false);
  const [tags, setTags] = useState(windowIcon?   windowIcon : store.getState().icon);
  const [tagId, setTagId] = useState(1);

  const onSwitch = () => {
    settagSwitch(!tagSwitch);
  };
  const onEditor = () => {
    seteditorSwitch(!editorSwitch);
  };

  const onIcon = (id: number, icon: string,name:string) => {
    const action = {
      type: 'icon_index',
      value: {
        id:id,
        icon:icon,
        name:name
      }
    };
    store.dispatch(action);
    setTagId(id);
  };

  const onDelete = () => {
    const action = {
      type: 'delete_icon',
      tagId: tagId
    };
    store.dispatch(action);
    const  windowIcon =JSON.parse(window.localStorage.getItem('icon') as string)
    setTags(windowIcon);
    settagSwitch(!tagSwitch);
  };




  const modifies = (
    <div className="modifies-overlay">
      <div className="modifies-wrapper">
        <div className="modifies">
          <header>
            删除或编辑?
          </header>
          <main>
            <button onClick={onDelete}>
              删除
            </button>

            <button>
              <NavLink to={'/bookkeeping/' + tagId}>
                编辑
              </NavLink>
            </button>
            <button onClick={onSwitch}>
              取消
            </button>
          </main>

        </div>
      </div>
    </div>
  );

  const editor = (
    <div className="editor" onClick={onSwitch}>
      <Icon name="bianji"/>
    </div>
  );


  return (
    <Tags>
      <div>
        <ol>
          <li>
            选择分类
          </li>
          <li onClick={onEditor}>
            编辑
          </li>
        </ol>
        {
          tagSwitch ? modifies : ''
        }
      </div>
      <ul>
        {
          tags.map((tag: { id: number; icon: string; name: string; }) =>
            <li
              key={tag.id}
              onClick={() => onIcon(tag.id,tag.icon,tag.name)}
            >
              <Icon name={tag.icon}/>
              {editorSwitch ? editor : ''}
              <div>{tag.name}</div>
            </li>
          )
        }
        <li>

          <NavLink to="/Increase">
            <Icon name="tianja"/>
            <div>
              添加
            </div>
          </NavLink>

        </li>

      </ul>
    </Tags>
  );
};


export {TagsSection};