import Layout from '../../components/Layout';
import React from 'react';
import styled from 'styled-components';
import {Navigation} from './BookkeepingWrapper/Navigation';
import {Remarks} from './BookkeepingWrapper/Remarks';
import {BookkeepingWrapper} from './BookkeepingWrapper/BookkeepingWrapper';
import {TwoWrapper} from './BookkeepingWrapper/TwoWrapper';
import {DigitalStorage} from './BookkeepingWrapper/DigitalStorage/DigitalStorage';
import {TagsSection} from './BookkeepingWrapper/TagsSection/TagsSection';
import store from '../../store/store';

const MyLayout = styled(Layout)``;
const Save = styled.button`
width: 30%;
border-radius: 12px;
height: 30px;
border: none;
background: #7db2f8;
color: white;
font-size: 16px;
outline:none
`;
const HeightWrapper = styled.div`
margin-top: 10%;
display: flex;
justify-content: center;
align-items: center;
`;

function Bookkeeping() {

  const onSave = () => {
    let save = store.getState().middle;
    save.iconID = Math.random()
    const action = {
      type:'add_save',
      value:save
    }
    const  actionTwo = {
      type:'save_Clear',
    }
    if (save.numberpad === ''){
      alert('请输入金额')
    }else {
      store.dispatch(action)
      store.dispatch(actionTwo)
    }


  };


  return (
    <MyLayout className="AA">
      <BookkeepingWrapper>
        <Navigation/>
        <DigitalStorage/>

        <TwoWrapper>
          <TagsSection/>
          <Remarks/>

          <HeightWrapper onClick={onSave}>
            <Save>
              保存
            </Save>
          </HeightWrapper>


        </TwoWrapper>
      </BookkeepingWrapper>
    </MyLayout>
  );
}

export default Bookkeeping;