import Layout from '../components/Layout';
import React, {useState} from 'react';
import styled from 'styled-components';
import Icon from '../components/Icon';
import dayjs from 'dayjs';


const Wrapper = styled.section`
background: #E7E9Ef;
height: 100%;
display: flex;
flex-direction: column;
>ol{
background: #5084D4;
display: flex;
justify-content: center;
align-content: center;
text-align: center;
height: 50px;
  >li{
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
      &.selected{
      color: white;
      }
      &.selected:after{
      content: '';
        display:block; 
        height: 3px;
        background:red;
        position:absolute;
        bottom:0;
        width: 100%;
        left: 0;
  }
}
}

>div{
flex-grow: 1;
    >.itemWrapper{
overflow: auto;
display: flex;
flex-direction: column;
padding: 1%;

>h3{
margin: 2%;
font-size: 17px;
color: #5084D4;
}
>.item{
background: white;
border-radius: 14px;
margin: 3px;

>div{
display: flex;
margin-bottom: 2%;
justify-content: space-between;
align-items: center;
    >div{
    margin: 2%;
    font-size: 16px;
    }
    >.itemIcon{
    margin-left: 2%;
    display: flex;
    justify-content: center;
    align-items: center;
      >.icon{
      width: 2em;
      height: 2em;
      }
    }
       
    >.itemText{
    color: #999;
    margin-right: auto;
    }
    
    >.itemNumber{
    margin-left: 5px;
    }


}


}

}
  >.text{
    
    text-align: center;
    margin: 20px;
  }

}

  



`;


const Details: React.FunctionComponent = () => {
  type Save = { createAt: string; index: number; icon: string; tag: string; numberpad: number; text: string; iconID: number; name: string }
  const navigationMap = {'-': '支出', '+': '收入'};
  const [navigationList] = useState<('-' | '+')[]>(['-', '+']);
  const [navigationSwitching, setNavigationSwitching] = useState('-');
  const onNav = (c: string) => {
    setNavigationSwitching(c);
  };


  const win = JSON.parse(window.localStorage.getItem('save') as string);
  const hash: { [k: string]: Save[] } = {};
  const [details] = useState(win ? win : []);
  const detailsNav = details.filter((r: { tag: string }) => r.tag === navigationSwitching);


  detailsNav.map((r: Save) => {
    const key = r.createAt;
    if (!(key in hash)) {
      hash[key] = [];
    }
    hash[key].push(r)
    return ''
  });

  const arr = Object.entries(hash).sort((a, b) => {
    if (a[0] === b[0]) return 0;
    if (a[0] > b[0]) return -1;
    if (a[0] < b[0]) return 1;
    return 0;
  });


  const newDetail = () => {
    return arr.map(item =>
      <div  key={item[0]} className="itemWrapper">
        <h3>
          {  dayjs(item[0]).format('YYYY年MM月DD日')}
        </h3>
        <div className="item">
          {item[1].map(
            (t: Save) =>
              <div
                key={t.iconID}
              >
                <div className="itemIcon">
                  <Icon name={t.icon}/>
                </div>
                <div className="itemName">
                  {t.name}
                </div>
                <div className="itemText">
                  {t.text}
                </div>
                <div className="itemNumber">
                  ¥{t.numberpad}
                </div>
              </div>
          )
          }
        </div>

      </div>
    );
  };


  return (
    <Layout>
      <Wrapper>
        <ol>
          {
            navigationList.map(t =>
              <li key={t}
                  className={navigationSwitching === t ? 'selected' : ''}
                  onClick={() => onNav(t)}
              >
                {navigationMap[t]}
              </li>
            )
          }
        </ol>

        <div>
          {
            win ? newDetail() :
              <div  className="text">
                当前数据为空,请记一笔!
              </div>
          }
        </div>


      </Wrapper>
    </Layout>
  );
};
export default Details;