import dayjs from 'dayjs';

const middlestroe = {
  index: 1,
  icon: 'fushi',
  name: '服饰',
  tag: '-',
  numberpad: 0,
  text: '',
  iconID: 0,
  createAt: dayjs(new Date().toISOString()).format('YYYY-MM-DD')
};

function middle(state = middlestroe, action: any) {
  if (action.type === 'icon_index') {
    const newindex = JSON.parse(JSON.stringify(state));
    newindex.index = action.value.id;
    newindex.icon = action.value.icon;
    newindex.name = action.value.name;
    return newindex;
  }

  if (action.type === 'number_pad') {
    const newNumber = JSON.parse(JSON.stringify(state));
    newNumber.numberpad = parseFloat(action.numberpad);
    return newNumber;
  }
  if (action.type === 'Nav_tag') {
    const newNav = JSON.parse(JSON.stringify(state));
    newNav.tag = action.nav;
    return newNav;
  }
  if (action.type === 'text_remark') {
    const newRemark = JSON.parse(JSON.stringify(state));
    newRemark.text = action.remark;
    return newRemark;
  }
  if (action.type === 'save_Clear') {
    const newNumber = JSON.parse(JSON.stringify(state));
    newNumber.numberpad = '';
    return newNumber;
  }
  if (action.type === 'text_time') {
    const neWCreateAt = JSON.parse(JSON.stringify(state));
    neWCreateAt.createAt = action.onTime;
    return neWCreateAt;
  }


  return state;
}


export default middle;