import styled from 'styled-components';
import React, {useState} from 'react';
import Icon from '../../../components/Icon';
import store from '../../../store/store';

const Wrapper = styled.section`
display: flex;
align-items: center;
padding-top: 3%;
justify-content: space-between;
>div{
    text-align: right;
    width: 30%;
    >.icon{
    width: 2em;
    height: 2em;
    margin-left: 5%;
    }
}
>ul{
   display: flex;
   border-radius:35px;
   background: #5084D4;
   justify-content: center;
   align-items: center; 
   color: white;
   width: 40%; 
   height: 30px; 
    >li{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius:35px;
    width: 50%;
    height: 100%;
      &.selected{
        background: white;
        color: black;
       transition: all 0.5s ease-out;
      }
   }
   
}

`;


const Navigation: React.FunctionComponent = () => {

  const navigationMap = {'-': '支出', '+': '收入'};
  const [navigationList] = useState<('-' | '+')[]>(['-', '+']);
  const [navigationSwitching, setNavigationSwitching] = useState('-');

  const onNav = (c: string) => {
    setNavigationSwitching(c);
    const action = {
      type: 'Nav_tag',
      nav: c
    };
    store.dispatch(action)
  };


  return (
    <Wrapper>
      <div>
      </div>
      <ul>
        {navigationList.map(c =>
          <li
            key={c}
            className={navigationSwitching === c ? 'selected' : ''}
            onClick={() => onNav(c)}

          >
            {navigationMap[c]}
          </li>
        )}
      </ul>
      <div>
        <Icon name="moon"/>
      </div>
    </Wrapper>
  );
};


export {Navigation};